body {
	margin: 0;
	padding: 0;
	font-family: montserrat, sans-serif;
	background-color: #080808;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
  	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page-indicator {
	position: fixed;
	right: 20px;
	top: 50%;
	display: flex;
	flex-direction: column;
	margin-top: -20px;
}

.page-indicator span {
	height: 20px;
	width: 20px;
	border-radius: 100%;
	border: 1px solid white;
	margin: 10px;
	-webkit-transition: all 500ms ease;
	transition: all 500ms ease;
}

.active {
	background-color: white;
}

#icon-container {
	position: absolute;
	top: 50%;
	margin-top: -80px;
	right: 17%;
	margin-right: -90px;
	display: flex;
	align-items: center;
	z-index: 2;
}
#mobile-icon-container {
	position: relative;
	display: flex;
	align-items: center;
	z-index: 2;
}

.icon {
	height: 60px;
	width: 60px;
	font-size: 25px;
	border-radius: 100%;
	border: 1px solid white;
	display:flex;
	align-items: center;
	justify-content: center;
	color: white;
	margin: 20px;
	-webkit-transition: all 300ms ease;
	transition: all 300ms ease;
	cursor: pointer;
}

.icon:hover {
	background-color: white; /* IE 9 */
	-webkit-transform: scale(1.2,1.2 ); /* Safari */
	transform: scale(1.2, 1.2);
	color: black;
}


#canvas {
	position: fixed;
	top: 0;
}

#master-wrapper {
	height: 300%;
}

#header-main {
	position: absolute;
	left: 15%;
	font-size: 40px;
	top: 50%;
	margin-top: -100px;
	color: white;
}

#name {
	color: #f05f40;
}

#header-secondary {
	position: absolute;
	left: 15%;
	font-size: 30px;
	top: 50%;
	color: white;
}

#body {
	position: absolute;
	width: 100vw;
	height: 90vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
}
#mobile-body {
	position: absolute;
	width: 100vw;
	height: 90vh;
	margin-top: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
}

#section-title {
	z-index: 1;
	font-size: 30px;
	margin-top: 200px;
	color: #f05f40;
}

#section-body {
	z-index: 1;
	max-width: 720px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.show-card {
	-webkit-transition: all 300ms ease;
	transition: all 300ms ease;
	z-index: 1;
	margin-top: 20px;
	width: 100%;
	height: 70vh;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {box-sizing: border-box}

/* That's it: */
.mouse {
  display: block;
  position: fixed;
  margin: 0 auto;
  width: 26px;
  height: 46px;
  border-radius: 13px;
  border: 2px solid lightgrey;
  bottom: 20px;
  left: 50%;
  margin-left: -13px;
}
.mouse span {
  display: block;
  margin: 6px auto;
  width: 3px;
  height: 8px;
  border-radius: 50%;
  background: lightgrey;
  border: 1px solid transparent;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-name: scroll;
  animation-name: scroll;
}
@-webkit-keyframes scroll {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(16px);
    transform: translateY(16px);
  }
}
@keyframes scroll {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(16px);
    transform: translateY(16px);
  }
}

.Card {
	padding: 20px;	
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	-webkit-transition: all 300ms ease;
	transition: all 300ms ease;
}

.hide {
	margin: 20px;
	background-color:rgba(64, 64, 64, 0.5);
	color: white;
	cursor: pointer;
	max-width: 400px;
	min-width: 220px;
	-webkit-transition: all 300ms ease;
	transition: all 300ms ease;
}
.hide:hover {
	background-color: #f2f2f2;
	-webkit-transform: scale(1.1,1.1 ); 
	transform: scale(1.1, 1.1);
	color: black;
}

.show {
	width: 100%;
	min-height: 500px;
	background-color: #f2f2f2;
	color: black;
}

#close-button {
	font-size: 30px;
	cursor: pointer;
	margin-right: -90%;
}

#card-content {
	display: flex;
	flex-wrap: wrap;
}

#tool {
	border: 1px solid #f05f40;
	color: #f05f40;
	padding: 10px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 20px;
	margin: 5px;
}

#git-link {
	color: grey;
	text-decoration: none;
}

#card-type {
	border-radius: 15px;
	color: white;
	padding: 2px 20px;
	border: 1px solid white;
	margin-bottom: 10px;
}

#logo {
	margin-bottom: 20px;
}

#title {
	font-size: 20px;
	font-weight: bold;
}

#description {
	font-size: 20px;
}
